<template>
    <div>
        <div class="popup__content">
            <div class="add-worker__top mb24">
                <div class="text-size-l text-bold text-color">Сотрудник</div>
            </div>
            <div style=" width: 100%;">
                <div class="mb16">
                    <input-select
                    @change="form.selectGroup.error=false"
                    @open="form.selectGroup.error=false"
                    :tag="'div'"
                    :isError="form.selectGroup.error"
                    v-model="form.selectGroup.value"
                    :options="selectOptionsDepartments"
                    :placeholder="'Группа'"
                    :list-style="{'max-height': 'none'}"></input-select>
                </div>
                <div class="mb16">
                    <input-multi
                         @chunk="setPersonInput"
                         name="worker-name"
                         :is-error="form.worker.name.error"
                         :error-text="form.worker.name.errorText"
                         v-model="form.worker.name.value"
                         :fields="form.worker.name.fields"
                         @focus="removeError(form.worker.name)"
                         :placeholderInput="'Фамилия Имя Отчество'"
                         @blur="blur(form.worker.name)"
                         :placeholder="'Фамилия Имя Отчество'"></input-multi>
                </div>

                <div class="mb16">
                    <input-styled
                    name="worker-post"
                    :placeholder="'Должность'"
                    :placeholderInput="'Должность'"
                    v-model="form.worker.post.value"
                    @focus="form.worker.post.error=false"
                    :is-error="form.worker.post.error">
                    </input-styled>
                </div>

                <div class="flex-row mb24">
                    <div style="width: 204px;">
                        <input-styled
                        name="worker-phone"
                        :placeholder="'Телефон'"
                        :placeholderInput="'Телефон'"
                        v-model="form.worker.phone.value"
                        @focus="form.worker.phone.error=false"
                        :error-text="form.worker.phone.errorType === 'use' ? 'Этот номер уже существует':form.worker.phone.errorType === 'mobile' ? 'Только мобильные номера':''"
                        :is-error="form.worker.phone.error"
                        :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}">
                        </input-styled>
                    </div>

                    <div style="width: 204px">
                        <input-styled
                        name="worker-post"
                        :placeholder="'E-mail'"
                        :placeholderInput="'E-mail'"
                        v-model="form.worker.email.value"
                        :error-text="form.worker.email.errorType === 'use' ? 'Этот email уже существует':form.worker.email.errorType === 'rule' && form.worker.email.value.length ? 'Некорректный email':''"
                        @focus="form.worker.email.error=false"
                        :is-error="form.worker.email.error">
                        </input-styled>
                    </div>
                </div>
                <div class="group-accesses">
                    <template v-for="i in topSections">
                        <div class="item"  :key="i.id">
                            <span class="text-bold text-size-s">{{i.name}}</span>
                            <input-select-new
                            :tag="'div'"
                            v-model="form.rights[i.id]"
                            :options="options"
                            :placeholder="'Категория'"
                            class="borderless"
                            :list-style="{'max-height': 287 + 'px'}"></input-select-new>
                        </div>
                    </template>
                </div>
            </div>
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="" @click.native="send" :type="form.btnType">
                        <span class="i mr8" :class="{'i-ok7':form.btnType=='normal'}"></span>Сохранить
                    </submit-btn>
                </div>
                <div v-if="!editUser" class="row-b__col elem-pos-fix">
                    <div class="mt16"  id="cooperation-checkPersonal">
                        <input v-model="form.notifyWorker.value" type="checkbox" id="notify-worker">
                        <label for="notify-worker">Уведомить сотрудника</label>
                    </div>
                </div>
            </div>
            <div v-if="editUser && editUser.id && !editUser.banned" class="add-worker__link block">
                <a @click="userBanned" href="javascript:void(0);" class="link-icon link-icon__modal">
                    <span class="i i-stop"></span>
                    <span class="text">Заблокировать</span>
                </a>
            </div>
            <div v-else-if="editUser && editUser.id" class="add-worker__link block">
                <a  @click="userUnBanned" href="javascript:void(0);" class="link-icon link-icon__modal">
                    <span class="i i-unlock2"></span>
                    <span class="text">Разблокировать</span>
                </a>
            </div>
            <div v-if="editUser && editUser.id" class="add-worker__link remove">
                <a @click="userDelete" href="javascript:void(0);" class="link-icon link-icon__modal">
                    <span class="i i-trash2"></span>
                    <span class="text">Удалить</span>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import InputSelect from '@/components/forms/InputSelect.vue';
    import InputSelectNew from '@/components/forms/InputSelectNew.vue';
    import InputMulti from '@/components/forms/InputMulti.vue';
    import {scrollTo} from "@/helpers/animate";

    export default {
        name: "AddWorker",
        components: {
            InputStyled,
            SubmitBtn,
            InputSelect,
            InputMulti,
            InputSelectNew
        },
        mounted(){
            if(this.editUser.name){
                let fio = this.editUser.name.split(' ');
                this.form.worker.name.value = this.editUser.name;
                this.form.worker.name.fields.lastName.value = fio[0];
                this.form.worker.name.fields.firstName.value = fio[1] ? fio[1] : ' ';
                this.form.worker.name.fields.secondName.value = fio[2] ? fio[2] : ' ';
            }
            if(this.editUser.post){
                this.form.worker.post.value = this.editUser.post;
            }
            if(this.editUser.email){
                this.form.worker.email.value = this.editUser.email;
            }
            if(this.editUser.phone){
                this.form.worker.phone.value = this.editUser.phone;
            }
            if(this.editUser.department){
                this.form.selectGroup.value = this.getOptionsDepartmentsName(this.editUser.department);
            }
            if(this.editUser.id){
                for(let key in this.topSections){
                    let right = 0;
                    if(this.editUser.rights[this.topSections[key].id]){
                        if(this.editUser.rights[this.topSections[key].id] == 'R'){
                            right = 1;
                        }
                        if(this.editUser.rights[this.topSections[key].id] == 'W'){
                            right = 2;
                        }
                    }
                    this.$set(this.form.rights, this.topSections[key].id, right);
                }
            }
            else{
                for(let key in this.topSections){
                    this.$set(this.form.rights, this.topSections[key].id, 0);
                }
            }
        },
        data() {
            return {
                options: [
                  {name: '<span class=text-color-orange2>Читатель</span>', id: 1},
                  {name: '<span class=text-color-green>Редактор</span>', id: 2},
                  {name: '<span>Нет доступа</span>', id: 0}
                ],
                form:{
                    selectGroup: {
                        value: '',
                        error: false,
                    },
                    selectAccess: {
                        id: 3,
                        error: false,
                    },
                    worker: {
                        name: {
                            name: 'workerName',
                            value: '',
                            fields: {
                                lastName: {
                                    name: 'lastName',
                                    value: '',
                                    delimiter: ' ',
                                    placeholder: 'Фамилия',
                                },
                                firstName: {
                                    name: 'firstName',
                                    value: '',
                                    delimiter: ' ',
                                    placeholder: 'Имя',
                                },
                                secondName: {
                                    name: 'secondName',
                                    value: '',
                                    delimiter: '',
                                    placeholder: 'Отчество',
                                },
                            },
                            required: true,
                            error: false,
                            errorText: '',
                            disabled: false,
                            focus:false,
                            sortScroll: 7
                        },
                        post: {
                            value: '',
                            error: false,
                        },
                        phone: {
                            value: '',
                            error: false,
                            errorType: 'empty',
                        },
                        email: {
                            value: '',
                            error: false,
                            errorType: 'empty',
                        },

                    },
                    rights:{},
                    notifyWorker: {
                        value: false,
                        error: false,
                    },
                    btnType: 'normal',
                },
            }
        },
        methods: {
            setPersonInput: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.form.worker.name.fields[field.name].value = field.value;
                }
            },
            blur(field){
                field.focus = false;
            },
            removeError: function(field) {
                field.error = false;
                field.focus = true;
            },
            send(){
                let error = false;
                this.form.worker.phone.errorType = 'empty';
                this.form.worker.email.errorType = 'empty';

                if(!this.form.selectGroup.value || this.form.selectGroup.value.length == 0){
                    if(this.form.selectGroup.error){
                        this.form.selectGroup.error = false;
                        setTimeout(() => {
                            this.form.selectGroup.error = true;
                        });
                    }else{
                        this.form.selectGroup.error = true;
                    }
                    error = true;
                }
                if(this.form.worker.name.value.length == 0){
                    this.repeatFieldError('name');
                    error = true;
                } else {
                    for(let n in this.form.worker.name.fields){
                        if(this.form.worker.name.fields[n].value.length == 0){
                            this.repeatFieldError('name');
                            error = true;
                        }
                    }
                }
                if(this.form.worker.post.value.length == 0){
                    this.repeatFieldError('post');
                    error = true;
                }
                if(this.form.worker.phone.value.length == 0){
                    this.repeatFieldError('phone');
                    error = true;
                } else {
                    let phone = this.form.worker.phone.value.replace(/[^\+0-9]/g, '');
                    if (phone.length < 12) {
                        error = true;
                        this.repeatFieldError('phone');
                        if(phone.length > 0){
                            this.form.worker.phone.errorType = 'mobile';
                        }
                    } else if (phone[2] != '9') {
                        error = true;
                        this.repeatFieldError('phone');
                        this.form.worker.phone.errorType = 'mobile';
                    }
                }
                let rEmail = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                if(this.form.worker.email.value.length == 0 || !rEmail.test(this.form.worker.email.value)){
                    this.repeatFieldError('email');
                    if(!rEmail.test(this.form.worker.email.value)){
                        this.form.worker.email.errorType = 'rule';
                    }
                    error = true;
                }


                if(!error){
                    this.form.btnType = 'load';
                    let params = {
                        id: this.editUser.id ? this.editUser.id : false,

                        form:{
                            department: this.selectedDepartmentId,
                            firstName: this.form.worker.name.fields.firstName.value,
                            lastName: this.form.worker.name.fields.lastName.value,
                            secondName: this.form.worker.name.fields.secondName.value,
                            post: this.form.worker.post.value,
                            phone: this.form.worker.phone.value,
                            email: this.form.worker.email.value,
                            type: 'saveUser',
                            rights: JSON.stringify(this.form.rights),
                        }
                    };

                    this.$store.dispatch('rulesStaff/addUser', params).then((result)=>{
                        this.form.btnType = 'normal';

                        if(result.status == 'error'){
                            console.log(result)
                            for(let e in result.statusErrors) {
                                if(result.statusErrors[e] == 'phoneIsUsed'){
                                    this.repeatFieldError('phone');
                                    this.form.worker.phone.errorType = 'use';
                                }
                                if(result.statusErrors[e] == 'emailIsUsed'){
                                    this.repeatFieldError('email');
                                    this.form.worker.email.errorType = 'use';
                                }
                            }
                        } else {
                            this.$store.commit('popups/close', {name: this.popups.addWorker.name});
                            //На всякий случай очистим данные для редактирования раздела
                            this.$store.commit('rulesStaff/setEditUser',[]);
                            if(this.editUser.id){
                                this.$nextTick(()=>{
                                    scrollTo('#user_'+this.this.editUser.id);
                                })
                            }
                        }
                    });
                }
            },
            userBanned(){
                if(this.editUser.id){
                    this.$store.dispatch('rulesStaff/userBanned', {id: this.editUser.id})
                    this.$store.commit('popups/close', {name: this.popups.addWorker.name});
                }
            },
            userUnBanned(){
                if(this.editUser.id) {
                    this.$store.dispatch('rulesStaff/userUnBanned', {id: this.editUser.id});
                    this.$store.commit('popups/close', {name: this.popups.addWorker.name});
                }
            },
            userDelete(){
                if(this.editUser.id) {
                    this.$store.commit('popups/close', {name: this.popups.addWorker.name});
                    this.$store.commit('popups/open', {name: this.popups.removeWorker.name});
                }
            },
            getOptionsDepartmentsName(id){
                let this_ = this, result;

                this.usersGrouped.forEach(function(item, i, arr) {
                    if(item.id == id){
                        result = item.name;
                        return false;
                    }
                });

                return result;
            },
            repeatFieldError(field){
                if(this.form.worker[field].error){
                    this.form.worker[field].error = false;
                    setTimeout(() => {
                        this.form.worker[field].error = true;
                    });
                }else{
                    this.form.worker[field].error = true;
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                usersGrouped: state => state.rulesStaff.usersByDepartmentList,
                topSections: state => state.rules.topSections,
                editUser: state => state.rulesStaff.editUser,
            }),
            selectOptionsDepartments(){
                let result = [];
                this.usersGrouped.forEach(function(item, i, arr) {
                    result.push(item.name);
                });
                return result;
            },
            selectedDepartmentId(){
                let this_ = this, result;

                this.usersGrouped.forEach(function(item, i, arr) {
                    if(item.name == this_.form.selectGroup.value){
                        result = item.id;
                        return false;
                    }
                });
                return result;
            },
        },
    }
</script>

<style scoped>

</style>
