<template>
    <div class="inp-s-wrap full" :class="{ '--icon': isEdit || isValidate || isClearBtn, active: showLabel, 'inp-disabled': disabled, '--error': isError, 'repeat-error':  repeatError}">
        <span class="inp-s-label" :class="{ error: isError }">{{ placeholder }}</span>
        <input class="inp-s"
               :class="{ error: isError, disabled: disabled, 'repeat-error': repeatError }"
               type="text"
               ref="field"
               @keyup.enter="enter()"
               @keyup.delete="deleteInput($event)"
               @focus="setFocus(true,$event)"
               @blur="setFocus(false,$event)"
               :disabled="disabled"
               :value="value"
               :name="nameInput"
               v-mask="mask"
               :autocomplete="fieldAutocomplete"
               @input="updateValue($event.target)"
        >
        <span :style="stylePlaceholderComplex" v-show="showLabel" class="inp-s-placeholder" :class="{ error: isError }">{{ placeholderComplex }}</span>
        <span class="inp-s-error" v-if="isError">{{ errorText }}</span>

        <span class="field-clear__btn" v-if="isClearBtn" @click="clear" >
            <span class="i i-close2"></span>
        </span>

        <span ref="fieldBuffer" class="inp-buffer" v-html="valueBuffer"></span>

        <span class="inp-s-icon">
            <span class="i i-pencil2 --edit" v-if="isEdit" @click="edit"></span>
            <span class="i i-ok2" v-if="isValidate" @click="check"></span>
            <span class="i i-info3" v-if="isError"></span>
        </span>

    </div>
</template>

<script>
    export default {
        name: "InputMulti",
        props: {
            placeholder: {default: ''},
            placeholderInput: {default: ''},
            inputId: {default: ''},
            value: {default: ''},
            disabled: {default: false},
            isError: {default: false},
            isEdit: {default: false},
            isValidate: {default: false},
            clearSpecial: {default: true},
            name: {default: 'name'},
            errorText: {default: ''},
            mask: {default: ''},
            fieldAutocomplete: {default: 'nope'},
            isClearBtn: {default: false},
            fields: {
                default: function () {
                    return {
                        text: {
                            name: 'text',
                            value: '',
                            delimiter: '',
                            placeholder: 'text'
                        }
                    }
                }
            }
        },

        data: function () {
            return {
                type: 'text',
                focus: false,
                stylePlaceholderComplex: {'left':0},
                nameInput: '',
                valueBuffer: '',
                showPlaceholder: false,
                repeatError: false,
            }
        },
        watch: {
            isError(val, oldVal){
                if(oldVal){
                    this.repeatError = true;
                }
            }
        },
        computed: {
            showLabel() {
                let value = '';
                for(let f in this.fields){
                    value += this.fields[f].value
                }
                return this.focus || value.toString().length > 0;
            },
            placeholderComplex() {
                let value = '';
                for(let f in this.fields){
                    if(this.fields[f].value.length === 0){
                        let placeholder = this.fields[f].placeholder;
                        if(this.value.trim().substr(-1,1) ===',' && this.fields[f].placeholder.substr(0,1) === ','){
                            placeholder = placeholder.split(',').join('');
                        }
                        value += placeholder;
                        value += this.fields[f].delimiter;
                    }
                }
                return value.trim();
            },
        },
        mounted (){
            this.valueBuffer = this.value;
            this.$nextTick(function () {
                if(this.$refs['fieldBuffer']){
                    let width = this.$refs['fieldBuffer'].offsetWidth;
                    if(this.value > 0) {
                        width = +width + 7;
                    }
                    this.stylePlaceholderComplex = {left: (width + 5) + 'px'};
                }
            });
            if(this.name !== 'name'){
                this.nameInput = this.name;
            } else {
                this.nameInput = Math.random().toString(36).substr(2, 5);
            }
        },
        methods: {
            deleteInput: function(event) {
                if(event.target.selectionStart === 0) {
                    this.prev(event.target)
                }
            },
            updateValue: function (target) {
                let value = target.value.replace(/\s+/g,' ').replace(/^\s+/,'');
                if(this.clearSpecial){
                    let spec = [];
                    for(let f in this.fields) {
                        if(this.fields[f].postfix){
                            spec.push(this.fields[f].postfix);
                        }
                    }
                    let reg = new RegExp('[^a-zа-яё0-9\\s-'+spec.join("\\")+']','i');
                    value = value.replace(reg, '');
                }
                this.valueBuffer = value;

                let count = 0;
                for(let f in this.fields) {
                    count++;
                }
                let split = value.split(' ');
                let fields = [];
                for(let f in split) {
                    if(f < count - 1){
                        fields.push(split[f])
                    }
                }
                split.splice(0,count - 1)
                if(split.length > 0){
                    fields.push(split.join(' '))
                }

                let field;
                let i = 0;
                for(let f in this.fields) {
                    if(fields[i]){
                        this.$emit('chunk', {value: fields[i].trim().split(this.fields[f].postfix).join(''), name:this.fields[f].name});
                    } else {
                        this.$emit('chunk', {value: '', name:this.fields[f].name});
                    }
                    i++;
                    if(i === fields.length){
                        field = this.fields[f];
                    }

                }
                if(value.substr(-1,1) === field.postfix && this.value.substr(-1,1) !==' ') {
                    value += ' '
                }
                if(field && field.postfix && value.substr(-1,1) === ' ' && value.trim().substr(-1,1) !== field.postfix && this.value.substr(-1,1) !==field.postfix && field.value.trim().length === 0) {
                    value = value.trim() + field.postfix + ' '
                    this.valueBuffer = value;
                }
                this.$nextTick(function () {

                    let width = this.$refs['fieldBuffer'].offsetWidth;
                    if(value.length > 0  && this.placeholderComplex.substr(0,1) !== ',') {
                        width = +width + 7;
                    }
                    this.stylePlaceholderComplex = {left: (width + 5) + 'px'};
                })
                this.$emit('input', value);
            },
            enter: function () {
                this.$emit('enter');
            },
            check: function () {
                this.$emit('check')
            },
            clear: function() {
                this.$refs.field.focus();
                this.$emit('input', '');
            },
            next: function(target) {
                if(target.nextElementSibling) {
                    target.nextElementSibling.focus();
                }
            },
            prev: function(target) {
                if(target.previousElementSibling) {
                    target.previousElementSibling.focus();
                }
            },
            setFocus(value,event) {
                let self = this
                value = value || false;
                this.focus = value;
                this.repeatError = false;
                if(value){
                    this.$emit('focus');
                    if(event.target.previousElementSibling.value) {
                        if(event.target.previousElementSibling.value.length === 0){
                            event.target.previousElementSibling.focus();
                        }
                    }
                } else {
                    this.$emit('blur');
                }
            },
            edit: function () {
                this.$emit('edit');
            },

        }
    }
</script>

<style scoped>
    .inp-s-placeholder {
        color: rgba(84, 84, 84, 0.15);
        font-weight: bold;
        min-width: 3px;
        position: absolute;
        top: 0px;
        left: 0px;
        font-size: 20px;
        height: 36px;
        line-height: 36px;
    }
    .inp-s-label{
        z-index: 2 !important;
        top: 11px;
        left: 8px;
        font-size: 14px;
        display: block !important;
        pointer-events: none;
    }
    .inp-buffer {
        min-width: 3px;
        position: absolute;
        top: -500px;
        left: -500px;
        opacity: 0;
        font-size: 14px;
        font-weight: normal;
    }
</style>
